.treetable>table{border-collapse:collapse}tr.hidden td div{max-height:0px;-webkit-box-sizing:border-box;box-sizing:border-box;padding-top:0;padding-bottom:0;overflow:hidden}tr.hidden td{padding-top:0;padding-bottom:0;-webkit-transition:padding 1s ease;transition:padding 1s ease;border:0}tr.hidden{border:0}tr.hidden button{display:none}tr div{max-height:100px;-webkit-transition:max-height 0.6s ease, padding 0.6s ease;transition:max-height 0.6s ease, padding 0.6s ease}
